.container{
    display: flex;
    flex-direction: column;    
    font-size: 25px;    
    border-bottom: 2px solid black;
    text-align: left;     
    width: 100%;
    height: 50%;
    margin-left: 5px;
}
.title-container{
    display: flex;
    flex-direction: column;
    height: 10%;
}

.content-container{
    display: flex;
    flex-direction: column;
    height: 80%;
    white-space: pre-line;
}

.link-container{
    display: flex;
    flex-direction: column;
    max-height: 50px;
    height: 10%;
}
.img{
    margin-top: 5px;
    margin-bottom: 20px;
    max-width: 100%;
    max-height: 100%; 
    margin-right: 10px;
    filter: brightness(0) invert(1);
}
.row{
    height: 50px;
    display: inline-flex;
    
}
.p{
    margin: 0;
    padding: 0;
}

.hyperlink{
    text-decoration: none;
    color:rgb(255, 72, 0);
    font-weight: bold;
    
}
.hyperlink:hover,.hyperlink:active
{
    color:rgb(255, 234, 0);
    border-bottom: 2px solid yellow;
   
}
.hyperlink--cta{
    color:white;
    background: rgb(224, 120, 0);
    padding: 8px 16px;
    border-radius: 4px;
}
.hyperlink--cta:hover,.hyperlink--cta:active{
    background: white;
    border: none;
}
.title{
   margin: 0; 
}

