.inherit{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.title-container{
    display: flex;
    width: 100%;
    min-height: 10%;
 
    align-self: center;
    justify-content: center;
    align-items: center;
    text-align: center;    
    border-bottom: 1px gold solid;
}
.title-container-alone{
    display: flex;
    width: 100%;
    height: 100%;
    align-self: center;
    justify-content: center;
    align-items: center;
    text-align: center;    
    border-bottom: 1px gold solid;
}
.image-portrait{
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px;
    max-width: 100%;
    min-height: 80%;
    object-fit: contain;
}