.experience-container{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    
    width: 100%;
    border: 2px solid grey;
    margin: 20px 0px;
}
.content-container{
    min-height: 400px;
}
.image-container{
    height: 35%;
   
    display: flex;
    flex-direction: column;
   
}
.fit-image{
    max-height:100%; 
    max-width:100%;
    object-fit: contain;
}
.title-container{
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: column;
    border-top: 2px solid khaki;
    border-bottom: 2px solid khaki;
    
}
.title{
    margin: 10px 0px;
}

.center-text{
    display: flex;
    align-items: center;   
    align-self: center;
    padding: 5px;
    
}

.description-container{
    width: 100%;
   
    display: flex;
    flex-direction: column;
}
.description-content{
    margin: 0px;
    height: 90%;
    font-size: 22px;
}
.read-more{
    font-size: 20px;
    display: flex;
    flex-direction: column;
    height: 10%;
}
.hyperlink{
    text-decoration: none;
    color:rgb(255, 72, 0);
    font-weight: bold;
    padding-left: 5px;
    margin-bottom: 10px;
}
.hyperlink-not-ready{
    text-decoration: none;
    color:rgb(255, 72, 0);
    font-weight: bold;
    padding-left: 5px;
    margin:0;
    margin-bottom: 10px;
}
.button {
    transition-duration: 0.4s;
    background-color: #f44336;
    color: rgb(255, 255, 255);

    font-size: 15px;
    border: 2px solid #020202; /* Green */
    border-radius: 8px;
    
  }
.button-image {
  background: transparent;
  transition-duration: 0.4s;  
  }
.button-image:hover {
    background-color: #661e19;
    transition-duration: 0.4s;  
    }
    
  
.button:hover {
    background-color: #661e19; /* Green */
    color: white;
  }

  @media (min-width: 40rem){

}

@media (min-width: 80rem){
    .experience-container{
        display: inline-flex;
        height: 600px;
        width: 665px;
        margin: 20px 40px;
    }
    .content-container{
        display: flex;
    flex-direction: column;
    width: 100%;
    height: inherit;
    }
    .image-container{
        height: 55%;
    }
    .title-container{
        height: 15%;
    }
    .title{
        margin: 15px 0px;
    }
    .description-container{
        height: 30%;
    }
    .description-content{
        height: 80%;
    }
    .read-more{
        height: 20%;
    }
        
}