.skills{
    display: flex;
    flex-direction: column;
    text-align: left;
    padding: 10px 0 px;
    height: 30px;    
    width: 100%;
    margin: 5px 0px;
    padding-bottom: 5px;
    border-bottom: 1px solid orangered;

}
.content-title{
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    font-size: 10px;
    min-width: 10%;
    padding: 0;
    margin: 0;
    overflow: visible;
    float:right;
}
.inline{
    display: inline-flex;
}

@media (min-width: 40rem){

}

@media (min-width: 80rem){
    .skills{
        height: 50px;
        width: 1000px;
    }
    .content-title{
        font-size: 30px;
        width: 150px;
    }
}