.content-container{
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    min-width: 10%;
    font-size: 8px;
    margin: 2px;
          
}
.icon{
    display: flex;
    flex-direction: row;
    align-items: center;
    filter: brightness(0) invert(1);
    height: 20px;
    
}
.content-text{
    
    margin-left: 2px;
    margin-top: 5px;
    margin-bottom: 0;    
    height: inherit;
    

 
}
@media (min-width: 40rem){

}

@media (min-width: 80rem){
    .content-container{
        width: auto;
        font-size: 25px;
    }
    .icon{
        margin-left: 10px;
        height: 40px;
    }
}