*{
  box-sizing: border-box;
}
.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: calc(10px + 2vmin);
  color: white;
}
.center-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: center;
}

.h3,h2{
  border-bottom: 2px rgb(255, 72, 0) solid;
  margin: 10px;
}


@media (min-width: 40rem){


}

@media (min-width: 80rem){
  .center-container{
    width: 1400px;
  }
}