.history-container{
    display: inline-flex;
    text-align: right;
    list-style: none;
    align-items: center;
    align-self: flex-start;
    width: 100%;
    padding: 0;
    height: 300px;
   
}
.date-container{
    display: inline-flex;
    
    height: 75px;
    width: 100px;
}
.date-text{
    margin: 20px;
}
.details-container{
    
    margin-left: 10px;
    display: inline-flex;
    border-left: 2px solid rgb(255, 30, 0);
    margin-left: 5px;
    height: 100%;
    width: 100%;
    text-align: left;
    
}
.details-content{
    
    display: flex;
    height: inherit;
    width: inherit;
    flex-direction: row;
   
}
.details-text-container{
  
    display: inline-flex;
    flex-direction: row;
    height: inherit;
    width: 60%;
    
}
.details-text-container-no-image{
   
    display: inline-flex;
    flex-direction: row;
    height: inherit;
    border-right: 2px solid grey;;
    width: 100%;
}
.details-text-wrapper{
    
    display: flex;
    flex-direction: column;
    height: inherit;
    width: 100%;
}
.details-text,.details-text-charge,.details-text-title,.details-text-desc,.details-text-hyperlink{
    padding: 0;
    margin: 0;
}
.details-text-title{
    margin-left: 5px;
    margin-bottom: 5px;
}
.details-text-charge{
    margin-left: 10px;
    font-size: 12px;
    margin-bottom: 5px;
}
.details-text-desc{
    margin-left: 5px;
    font-size: 12px;
    
}
.details-text-hyperlink{
    top: 25px;
    font-size: 15px;
    position: relative;
        
}
.title-segment{
    display: flex;
    flex-direction: column;
    height: 10%;
    width: 100%;
    border-top: 2px solid grey;
  
}

.charge-segment{
    display: flex;
    flex-direction: column;
    height: 10%;
    width: 100%;
    margin-bottom: 5px;
    
}
.desc-segment{
    display: flex;
    flex-direction: column;
    min-height: 65%;
    width: 100%;
    margin-bottom: 5px;
   
}
.read-more-segment{
    display: flex;
    flex-direction: column;
    height: 15%;
    width: 100%;
    border-top: 3px solid rgba(246, 255, 0, 0.805);
}

.hyperlink{    
        text-decoration: none;
        color:rgb(255, 72, 0);
        font-weight: bold;        
        margin-bottom: 10px;
        font-size: 14px;    
}
.hyperlink-not-ready{
    
    text-decoration: none;
    color:rgb(255, 72, 0);
    font-weight: bold;        
    margin: 0;
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 14px;

}
.hyperlink > .hyperlink:last-child{
    border-bottom: 6px solid grey;
}

.image-container{
 
    display: inline-flex;    
    align-self: center;
    height: inherit;
    width: 40%;
    border-top: 2px solid grey;
    border-left: 2px solid grey;
    border-right: 2px solid grey;
}

.button {
    transition-duration: 0.4s;
    background-color: #f44336;
    color: rgb(255, 255, 255);
    font-size: 15px;
    border: 2px solid #020202; /* Green */
    border-radius: 8px;    
}
.button:hover {
    background-color: #661e19; /* Green */
    color: white;
}

.button-image {
    background: transparent;
    transition-duration: 0.4s;  
    height: 100%;
    width: 100%;
    border: 0px;
}
.button-image:hover {
    background-color: #661e19;
    transition-duration: 0.4s;  
    }

.center {
    display: block;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    
  }
.center-button {
    display: block;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    width:100%;
      }

@media (min-width: 40rem){

    }
    
@media (min-width: 80rem){
    .history-container{
        padding: auto;        
    }
    .title-segment{
        height: 15%;
        margin-bottom: 5px;
    }
    .desc-segment{
        min-height: 60%;
    }
    .read-more-segment{
        height: 20%;
    }
    .charge-segment{
        margin-bottom: 0px;
    }
    .details-text-container{
        width: 80%;
    }
    .date-container{
        height: 150px;        
    }
    .details-text-charge{
        font-size: 15px;
    }
    .details-text-desc{
        font-size: 20.5px;
    }
    }
    