.modal-body{
    height: 100%;
    width: 100%;
    position: fixed;
   
   
}
.overlay{
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 5;
 
  }

  .modal-container{
    display: flex;
    flex-direction: column;
    max-width: 75%;
    max-width: 1400px;
    width: 100%;
    position: fixed;
    top: 50%;
    left: 50%;
    height:99%;
    transform: translate(-50%,-50%);
    background-color: #616060;
    box-shadow: 0px 0px 18px 0px rgba(238, 146, 48, 0.75);
    border-radius: 5%;
    z-index: 20;
    overflow: auto;
    
}

.close-button{
    display: flex;
    position: absolute;
    top: 15px;
    right: 5px;
    z-index: 25;
    filter: brightness() invert(1) drop-shadow(0px 0px 1px red);

}

.fixed-pos{
    position: fixed;
}
.remove-margin{
    margin: 0;
    padding: 0;
}

.portrait{
    width: 100%;
    height: auto;
}
.close-icon{
    height: 50px;
   
    
}
